import React, { useContext } from "react"
import { Link } from "gatsby"
import styled from "styled-components"

import ContentWrapper from "../styles/contentWrapper"
import Context from "../context"
import Logo from "./logo"
import GatsbyMonogram from "./GatsbyMonogram.png"
import { lightTheme, darkTheme } from "../styles/theme"
import { footerLinks } from "../../config"

const StyledFooter = styled.footer`
  width: 100%;
  height: ${({ theme }) => theme.footerHeight};
  background: ${({ theme, darkMode }) =>
    darkMode ? theme.colors.background : theme.colors.primary};
  border-top: ${({ theme, darkMode }) =>
    darkMode ? `3px solid ${theme.colors.boxShadowHover}` : null};
  margin-top: 10rem;
`

const StyledContentWrapper = styled(ContentWrapper)`
  && {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .footer-links {
      /* Adjust width of links wrapper accordingly */
      width: 10rem;
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      align-items: center;
      @media (min-width: ${({ theme }) => theme.breakpoints.sm}) {
        width: 15rem;
      }
    }
  }
`

const StyledLink = styled(Link)`
  font-size: 0.875rem;
  font-weight: 700;
  text-transform: uppercase;
  color: ${({ theme, $darkMode }) =>
    $darkMode ? theme.colors.primary : theme.colors.background};
  letter-spacing: 1px;
`

const Footer = () => {
  const { darkMode } = useContext(Context).state
  return (
    <StyledFooter darkMode={darkMode}>
      <StyledContentWrapper>
        <Link to="/" aria-label="home">
          <Logo
            size="1.5rem"
            color={
              darkMode ? darkTheme.colors.primary : lightTheme.colors.background
            }
          />
        </Link>
        <div className="footer-links" data-testid="footer-links">
          {footerLinks.map(({ name, url }, key) => (
            <StyledLink key={key} to={url} $darkMode={darkMode}>
              {name}
            </StyledLink>
          ))}
        </div>
      </StyledContentWrapper>
      <div
        className="footer_attribution_container"
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          width: "100%",
          height: "5rem",
        }}
      >
        <a href="https://www.gatsbyjs.com/" rel="noopener noreferrer">
          <img
            src={GatsbyMonogram}
            alt="Gatsby monogram"
            style={{ width: "1rem", height: "1rem" }}
          />
        </a>
        <p
          style={{
            fontSize: "0.875rem",
            fontWeight: "700",
            marginBottom: 0,
            color: darkMode
              ? darkTheme.colors.primary
              : lightTheme.colors.background,
          }}
        >
          &nbsp;Theme by&nbsp;
        </p>
        <StyledLink
          to={
            "https://github.com/konstantinmuenster/gatsby-starter-portfolio-minimal"
          }
          $darkMode={darkMode}
          style={{ textTransform: "none" }}
        >
          Konstantin Muenster
        </StyledLink>
      </div>
    </StyledFooter>
  )
}

export default Footer
